.contributors {
  margin-top: 46px;
}

.contributors__title {
  margin-bottom: 33px;
  letter-spacing: -0.9px;
  padding-bottom: 31px;
  border-bottom: 2px solid transparent;
  border-image: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 60%);
  border-image-slice: 1;
}

.contributors__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 100px;
}

.contributors__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contributors__name {
  padding-right: 350px;
  width: 100%;
  position: relative;
  font-size: 21px;
  line-height: 22px;
  font-weight: 700;
  letter-spacing: -0.9px;
  cursor: pointer;
  text-decoration: none;
  color: $main-text;
  opacity: 0.5;
  transition: opacity 0.5s ease;

  &:hover {
    opacity: 1;
  }

  &:focus {
    opacity: 1;
  }

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    width: 50px;
    height: 52px;
    transform: translateY(-50%);
    padding-right: 480px;

    background-image: url('../../img/arrow.png');
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
  }
}

.contributors__logo {
  margin-right: 300px;
}

.contributors__image {
  width: 135px;
  height: auto;
}