.about {
  display: grid;
  grid-template-columns: 1fr auto;
  border-bottom: 2px solid transparent;
  border-image: linear-gradient(270deg, #00132E 0%, rgba(0, 19, 46, 0) 80%);
  border-image-slice: 1;
  margin-bottom: 84px;

  &:first-child {
    margin-top: 114px;
  }

  &:last-child {
    margin-bottom: 242px;
  }
}

.about__description {
  display: flex;
  flex-direction: column;
}

.about__images {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 50px;
}

.about__title {
  font-size: 36px;
  line-height: 41px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 24px;
}

.about__text {
  margin: 0;
  margin-bottom: 114px;

  &--big {
    margin-bottom: 50px;
  }
}

.about__quote {
  font-style: italic;
  margin: 0;
  margin-bottom: 97px;

  &--big {
    margin-bottom: 78px;
  }
}

.about__author {
  margin: 0;
  font-weight: 700;
  margin-bottom: 35px;
  letter-spacing: -0.5px;
}

.about__image {
  margin-bottom: 36px;
  box-shadow: 8px 8px 4px rgba(0, 0, 0, 0.15);
}