.technology {
  display: grid;
  grid-template-columns: auto 1fr;
  margin-top: 90px;
  margin-bottom: 42px;
}

.technology__nav {
  max-width: 500px;
}

.technology__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-right: 48px;
}

.technology__item {
  cursor: pointer;
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  margin-bottom: 42px;
  padding-right: 70px;
  letter-spacing: -1.2px;
}

.technology__link {
  position: relative;
  display: inline-block;
  width: 100%;
  box-sizing: content-box;
  opacity: 0.5;
  transition: opacity 0.5s ease;

  &:hover {
    opacity: 0.8;
  }

  &::after {
    content: '';
    position: absolute;
    right: -18%;
    top: 50%;
    width: 50px;
    height: 52px;
    transform: translateY(-50%);

    background-image: url('../../img/arrow.png');
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
  }

  &--active {
    opacity: 1;
  }
}

.description_show {
  display: none;
}

.description__title {
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  letter-spacing: -1.2px;
  margin: 0;
  padding-bottom: 28px;
  border-bottom: 2px solid transparent;
  border-image: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 90%);
  border-image-slice: 1;
}

.description__text {
  line-height: 25px;
  margin-top: 24px;
  margin-bottom: 42px;
  letter-spacing: -0.5px;
}

.description__image {
  margin-bottom: 14px;
}