.header {
  background-image: url('../../img/header.jpg'),
                    linear-gradient(90deg, #012a4a 0%, #012a4a 45%, #004667 55%, #004667 100%);
  background-repeat: no-repeat;
  background-position: top center;
}

.header__top {
  display: flex;
  justify-content: space-between;
  margin-top: 35px;
  margin-bottom: 28px;
}

.header__text {
  color: $white;
  max-width: 500px;
  margin: 0;
  line-height: 25px;
}

.header__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 18px;
}

.header__item {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;

  &--language {
    padding-left: 10px;
    padding-right: 10px;
    min-width: 132px;
    min-height: 28px;
    background-image: url('../../img/language.svg');
    background-repeat: no-repeat;
    background-position: center 13%;
    background-size: auto;
    background-color: $transparent;
  }
}

.header__link {
  display: inline-block;
  text-decoration: none;
  color: $white;
  padding-top: 10px;
  padding-bottom: 10px;

  &:hover {
    color: $light-blue;
  }

  &:focus {
    color: $light-blue;
  }

  &:active {
    color: $light-blue;
  }

  &--active {
    color: $light-blue;
  }
}

.active_language {
  color: $light-blue;
}