.footer {
  background-image: url('../../img/footer.jpg'),
                    linear-gradient(90deg, #012a4a 0%, #012a4a 45%, #004667 55%, #004667 100%);
  background-repeat: no-repeat;
  background-position: top center;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
  margin-bottom: 46px;
}

.footer__logo {
  margin-top: -9px;
}

.footer__link {
  color: $white;
  text-decoration: none;
  font-style: italic;
  padding-right: 22px;
}